.select-field {
	position: relative;
	background: rgba(var(--shading), .1);
	border-radius: 3px;
}

.select-field__input {
	display: block;
	box-sizing: border-box;
	width: 100%;
	height: var(--field-height);
	padding: var(--gap-m);
	background: transparent;
	color: inherit;
	caret-color: transparent;
	cursor: pointer;
}

.select-field__input::placeholder {
	color: inherit;
}

/* option list */
.select-field__popup {
	position: absolute;
	top: calc(100% + var(--gap-s));
	left: 0;
	right: 0;
}

.select-field__list {
	list-style: none;
	display: flex;
	flex-direction: column;
	flex-wrap: nowrap;
	align-items: stretch;
}

.select-field__option {
	padding: var(--gap-m);
	background: transparent;
	transition: background-color .1s linear;
	cursor: pointer;
}

.select-field__option--nav {
	background: rgba(var(--shading), .05);
}

.select-field__option--selected {
	color: rgb(var(--accent));
}

.select-field__search {
	box-shadow: 0 2px 0 0 rgb(var(--accent));
}

/* arrow */
.select-field::after {
	--size: .5rem;
	content: ' ';
	position: absolute;
	top: calc((var(--field-height) - var(--size)) / 2);
	right: var(--gap-m);
	width: var(--size);
	height: var(--size);
	pointer-events: none;
	transform: rotate3d(0, 0, 1, 135deg);
	transition: transform .5s ease;
	border-radius: 2px;
	opacity: .7;
	background:
		linear-gradient(to left, rgb(var(--shading)) 20%, transparent 20%),
		linear-gradient(to bottom, rgb(var(--shading)) 20%, transparent 20%);
}

.select-field--open::after {
	transform: rotate3d(0, 0, 1, -45deg);
}
