.switch-field {
	display: flex;
	flex-wrap: nowrap;
	align-items: center;
	box-sizing: border-box;
	height: var(--field-height);
}

.switch-field__label {
	flex-grow: 1;
	text-overflow: ellipsis;
}

.switch-field__switch {
	--size: 1.2rem;
	box-sizing: content-box;
	width: calc(2 * var(--size));
	height: var(--size);
	overflow: hidden;
	border-radius: 999px;
	cursor: pointer;
	background: rgba(var(--shading), .1);
	border: var(--gap-s) solid transparent;
	outline: 1px solid transparent;
	transition:
		background-color .2s linear .1s,
		outline-color .1s linear;
}

.switch-field__switch:focus {
	outline-color: rgb(var(--accent));
}

.switch-field--on .switch-field__switch {
	background: rgb(var(--accent));
}

.switch-field__thumb {
	display: inline-block;
	width: 100%;
	height: 100%;
	border-radius: 999px;
	background: rgba(var(--shading), .4);
	transform: translate3d(-50%, 0, 0);
	transition:
		background-color .2s linear .1s,
		transform .2s ease;
}

.switch-field--on .switch-field__thumb {
	background: rgb(var(--base));
	transform: translate3d(50%, 0, 0);
}
