.node-shape-field {
	display: flex;
	gap: var(--gap-s);
}

.node-shape-field__button {
	background: transparent;
	font-size: 0;
	padding: var(--gap-s);
	vertical-align: middle;
	transition: background-color .1s linear;
	border-radius: 3px;
	cursor: pointer;
}

.node-shape-field__button--selected,
.node-shape-field__button:focus,
.node-shape-field__button:hover {
	background: rgba(var(--shading), .1);
}

.node-shape-field__preview {
	--size: 1.5rem;
	width: var(--size);
	height: var(--size);
	fill: rgba(var(--shading), .4);
}

.node-shape-field__button--selected > .node-shape-field__preview {
	fill: rgb(var(--accent));
}
